body {
  background: #3A5BA6;
}

.menu-options {
  position: absolute;
  z-index: 10;
  left: 0%;
  width: 100%;
  height: 100%;
  background: #C4E3F8;
  margin: 0;
  padding: 0;
  display: inline-block;
  -webkit-animation: 0.3s agrandar 1;
          animation: 0.3s agrandar 1;
}

@-webkit-keyframes agrandar {
  0% {
    opacity: 0%;
    left: 100%;
  }
}

@keyframes agrandar {
  0% {
    opacity: 0%;
    left: 100%;
  }
}
.GameOver {
  position: absolute;
  z-index: 11;
  width: 100%;
  height: 100%;
  background: #C4E3F8;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  font-size: 2em;
}
.GameOver p {
  padding: 10%;
  text-align: center;
}
.GameOver button {
  width: 100%;
  height: 100%;
  background: #3A5BA6;
  margin: 0;
  padding: 0;
  width: 50%;
  height: auto;
  padding: 5%;
  color: #ffffd2;
  font-size: 0.5em;
  border-style: none;
  text-decoration: none;
}

.botonSuperior {
  display: flex;
  justify-content: flex-end;
}

.boton-menuP {
  background: none;
  border-style: none;
  margin-top: 1.5em;
}

.boton-menuP:focus {
  outline-style: none;
}

.opc {
  padding-top: 50%;
