  color: #3A5BA6;
  max-width: 100%;
  min-height: 100%;
  display: flex;
  flex-flow: column wrap;
  align-content: stretch;
}
.Criatura header {
  width: 100%;
  height: 8%;
  background: #3A5BA6;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
}
.Criatura header div {
