  width: 100%;
  height: 100%;
  background: none;
  margin: 0;
  padding: 0;
  color: #ccc;
  border-style: none;
  text-align: center;
  font-size: 1em;
}
.Criatura h1 input:focus {
  outline-style: none;
}
.Criatura fondo {
  width: 100%;
  height: 55%;
