.parpadeo {
  color: #f79bff;
  -webkit-animation: 0.7s parpadear infinite;
          animation: 0.7s parpadear infinite;
}

@-webkit-keyframes parpadear {
  70% {
    color: #3A5BA6;
  }
}

@keyframes parpadear {
  70% {
    color: #3A5BA6;
  }
}
.subidon {
  color: #a0ff9b;
  -webkit-animation: 0.7s subidon infinite;
          animation: 0.7s subidon infinite;
}

@-webkit-keyframes subidon {
  70% {
    font-size: 1.3em;
  }
}

@keyframes subidon {
  70% {
    font-size: 1.3em;
  }
}
